@mixin fontStyle(
    $fontSize: 16px,
    $fontWeight: 400,
    $lineHeight: 20px,
    $color: $color-black,
	$fontStyle: normal
) {
    font-weight: $fontWeight;
    font-size: $fontSize;
    line-height: $lineHeight;
    color: $color;
    font-style: $fontStyle;
}

@mixin flexStyle(
    $alignItems: flex-start,
    $justifyContent: flex-start,
    $flexDirection: row,
    $gap: 0,
	$flexWrap: nowrap
) {
    display: flex;
    flex-wrap: $flexWrap;
    flex-direction: $flexDirection;
    align-items: $alignItems;
    justify-content: $justifyContent;
    gap: $gap;
}

@mixin imgFullSize($width: 100%, $height: 100%, $objectFit: cover) {
    display: block;
    width: $width;
    height: $height;
    object-fit: $objectFit;
}

@mixin transitionStyle() {
    transition: all 0.5s ease;
}
